<template>
  <nav>
    <div class="container">
      <div class="nav-wrapper">
        <a href="/" class="brand-logo"><div id="logo" ></div></a>
        <a href="#" data-activates="mobile-demo" class="button-collapse"><i class="material-icons">menu</i></a>
        <ul class="right hide-on-med-and-down">
          <li>
            <router-link class="nav-link" to="converter">{{ $t('Converter')}}</router-link>
          </li>
          <li>
            <router-link class="nav-link" to="keyboard">{{ $t('Keyboard')}}</router-link>
          </li>
          <li>
            
            <a v-on:click="changeLanguage" ><span class="nav-link">{{ getLanguageText }}&nbsp;</span> <img :src="getLanguageIcon"  class="langIcon align-middle"></a>
          </li>
        </ul>
        <ul class="side-nav" id="mobile-demo">
          <li>
            <router-link class="nav-link" to="converter">{{ $t('Converter')}}</router-link>
          </li>
          <li>
            <router-link class="nav-link" to="keyboard">{{ $t('Keyboard')}}</router-link>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
  import Vue from 'vue' 

  export default {
    name: 'Header',
    data() {
      return {
        msg: 'This is Header'
      }
    },
    methods: {
      changeLanguage: function () {
        let locale = Vue.i18n.locale()
        if (locale == 'ge')
          Vue.i18n.set('en')
        else
          Vue.i18n.set('ge')
        
      },
    },
    computed: {
      getLanguageIcon: function() {
        let locale = Vue.i18n.locale()
        return locale == 'en' ? "/static/icons/ge.png" : "/static/icons/uk.png" 
      },
      getLanguageText: function() {
        let locale = Vue.i18n.locale()
        return locale == 'en' ? "ქართულად" : "in English" 
      }
    }
  }

</script>

<style>
  nav {
    z-index: 100;
    background-color: #3f51b5 ;
  }

  .nav-link {
    opacity: 0.6;
    transition: 0.3s;
  }

  .nav-link:hover {
    opacity: 1;
    transition: 0.3s;
  }

  .nav-link.router-link-active {
    opacity: 1;
    transition: 0.3s;
  }
  .langIcon {
    margin-bottom: -12px;
    user-select: none;
    cursor: pointer;
    width: 35px;
  }
  #logo
  {
    margin: 10px;
    height: 45px;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("../assets/imgs/logo_300.png");
    width: 45px;

  }
</style>


