<template>
    <div class="container">
        <div class="converter">
            <div class="converterBackground"></div>
            <div class="scriptSwitcher">
                <div class="dropdown">
                    <!-- Dropdown Trigger -->
                    <a class='dropdown-button btn btn-large indigo' data-activates='dropdown-input'>{{ inputScript | wordify("from")}}</a>

                    <!-- Dropdown Structure -->
                    <ul id='dropdown-input' class='dropdown-content'>
                        <li><a class="black-text darken-4" v-on:click="changeInput" data-script="მხედრული">{{ $t('Mkhedruli')}}<span v-on:click="changeInput" data-script="მხედრული" class="scriptIcon">აბგ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeInput" data-script="ასომთავრული">{{ $t('Asomtavruli')}}<span v-on:click="changeInput" data-script="ასომთავრული" class="scriptIcon">ႠႡႢ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeInput" data-script="ნუსხური">{{ $t('Nuskhuri')}}<span v-on:click="changeInput" data-script="ნუსხური" class="scriptIcon">ⴀⴁⴂ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeInput" data-script="მთავრული">{{ $t('Mtavruli')}}<span v-on:click="changeInput" data-script="მთავრული" class="scriptIcon">ᲐᲑᲒ</span></a></li>
                        <li class="divider"></li>
                        <li><a class="black-text darken-4" v-on:click="changeInput" data-script="ლათინური">{{ $t('Latin')}}<span v-on:click="changeInput" data-script="ლათინური" class="scriptIcon">Abc</span> </a></li>
                        <li><a class="black-text darken-4" v-on:click="changeInput" data-script="კირილური">{{ $t('Cyrillic')}}<span v-on:click="changeInput" data-script="კირილური" class="scriptIcon">Абв</span> </a></li>
                    </ul>
                </div>

                <span id="reverseBtn" :class="{disabled:!isSwitchDisabled}" v-on:click="switchScripts"><i class="a-reverse"></i></span>

                <div class="dropdown">
                    <!-- Dropdown Trigger -->
                    <a class='dropdown-button btn btn-large indigo' data-activates='dropdown-output'>{{ outputScript | wordify("to")}}</a>

                    <!-- Dropdown Structure -->
                    <ul id='dropdown-output' class='dropdown-content'>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="მხედრული">{{ $t('Mkhedruli')}}<span v-on:click="changeOutput" data-script="მხედრული" class="scriptIcon">აბგ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="ასომთავრული">{{ $t('Asomtavruli')}}<span v-on:click="changeOutput" data-script="ასომთავრული" class="scriptIcon">ႠႡႢ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="ნუსხური">{{ $t('Nuskhuri')}}<span v-on:click="changeOutput" data-script="ნუსხური" class="scriptIcon">ⴀⴁⴂ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="მთავრული">{{ $t('Mtavruli')}}<span v-on:click="changeOutput" data-script="მთავრული" class="scriptIcon">ᲐᲑᲒ</span></a></li>
                        <li class="divider"></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="ხუცური">{{ $t('Khutsuri')}}<span v-on:click="changeOutput" data-script="ხუცური" class="scriptIcon">Ⴀⴁⴂ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="შანიძისეული">{{ $t('Shanidze style')}}<span v-on:click="changeOutput" data-script="შანიძისეული" class="scriptIcon">Ⴀბგ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="ტფილელისეული">{{ $t('Tfileli style')}}<span v-on:click="changeOutput" data-script="ტფილელისეული" class="scriptIcon">Აბგ</span></a></li>
                        <li class="divider"></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="ფონეტიკური">{{ $t('Phonetic')}}<span v-on:click="changeOutput" data-script="ფონეტიკური" class="scriptIcon">ɔp'ʒ</span></a></li>
                        <li><a class="black-text darken-4" v-on:click="changeOutput" data-script="ტრანსკრიფცია">{{ $t('Transcription')}}<span v-on:click="changeOutput" data-script="ტრანსკრიფცია" class="scriptIcon">op'zh</span></a></li>
                    </ul>
                </div>

            </div>

            <div class="boxes">
                <div class="inputarea boxarea">
                    <transition-group name="flip-list" tag="div" :class="{scripts:true, on:isInputItemsActive}">
                        <span v-for="(item, index) in inputItems" :class="{ 'active': index === inputItems.length-1 }" v-on:click="changeInputItem($event.target.innerText)"
                            v-bind:key="item" class="script">{{ item }}</span>
                    </transition-group>
                    <div class="inputbox">

                        <textarea autofocus v-on:input="inputHandler" v-bind:style="{ 'font-size' : computedFontSize }" v-model="inputText" :placeholder="$t('შეიყვანეთ ტექსტი აქ...')"></textarea>
                        <div class="actions">
                            <span class="indicator">{{phonetic}}</span>
                            <span class="action waves-effect waves-silver a-interpret" :class="{active: isInterpreterOn }" v-on:click="handleInterpreter"></span>
                            <span class="action waves-effect waves-silver a-keyboard" v-on:click="unsupportedFunctionalle"></span>
                        </div>
                    </div>
                </div>
                <div class="outputarea boxarea">
                    <transition-group name="flip-list" tag="div" :class="{scripts:true, on:isOutputItemsActive}">
                        <span v-for="(item, index) in outputItems" :class="{ 'active': index === outputItems.length-1 }" v-on:click="changeOutputItem($event.target.innerText)"
                            v-bind:key="item" class="script">{{ item }}</span>
                    </transition-group>
                    <div class="outputbox">

                        <textarea v-model="outputText" v-bind:style="{ 'font-size' : computedFontSize }"></textarea>
                        <div class="actions">
                            <span class="indicator">{{lettersCount}}</span>
                            <span class="action waves-effect waves-silver a-copy" v-on:click="copyToClipboard"></span>
                            <!--<span class="action waves-effect waves-silver a-link"></span>-->
                            <span class="action waves-effect waves-silver a-star" v-on:click="unsupportedFunctionalle"></span>
                        </div>
                    </div>
                </div>
            </div>

            <div class="social">
                <div class="fb-like" data-href="https://www.facebook.com/anbani.ge/" data-layout="standard" data-action="recommend" data-size="large"
                    data-show-faces="true" data-share="true"></div>
                <div class="fb-like fb-like-mobile" data-href="https://www.facebook.com/anbani.ge/" data-layout="button_count" data-action="recommend" data-size="large"
                    data-show-faces="true" data-share="true"></div>
            </div>

        </div>
    </div>
</template>

<script>
import * as toolkit from "../scripts/converter.js";
import $ from "jquery";
import Vue from "vue";
export default {
  updated() {
    console.log("updated called");
  },

  mounted() {
    (function(d, s, id) {
      var js,
        fjs = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) return;
      js = d.createElement(s);
      js.id = id;
      js.src =
        "//connect.facebook.net/en_US/sdk.js#xfbml=1&version=v2.9&appId=816355445126136";
      fjs.parentNode.insertBefore(js, fjs);
    })(document, "script", "facebook-jssdk");

    try {
      FB.XFBML.parse();
    } catch (e) {
      console.log(e);
    }

    (function($) {
      $(function() {
        $(".button-collapse").sideNav();

        $(".dropdown-button").dropdown({
          inDuration: 300,
          outDuration: 225,
          constrainWidth: false, // Does not change width of dropdown to that of the activator
          hover: false, // Activate on hover
          gutter: 0, // Spacing from edge
          belowOrigin: false, // Displays dropdown below the button
          alignment: "left", // Displays dropdown with edge aligned to the left of button
          stopPropagation: false // Stops event propagation
        });
      }); // end of document ready
    })(jQuery); // end of jQuery name space

    if (this.likeUs == undefined || this.likeUs == true){
      this.likeUs = false
      setTimeout(() => {
        Materialize.toast(
          Vue.i18n.translate("<div align='right'>თუ მოგწონთ anbani.ge და გაინტერესებთ სიახლეები პროექტის შესახებ, <br>დაგვემატეთ ფეისბუქის გვერდზე <a href='http://fb.com/anbani.ge'>fb.com/anbani.ge</a></div>"),
          10000
        );
        this.likeUs = true
      }, 60000);
    }
      
  },

  name: "Converter",
  data() {
    return {
      isInterpreterOn: true,

      inputText: null,
      outputText: "",

      inputScript: "მხედრული",
      outputScript: "ასომთავრული",
      outputItems: ["ᲐᲑ", "აბ", "ⴀⴁ", "ႠႡ"],
      inputItems: ["Аб", "Ab", "აბ"],

      allowedInputs: [
        "მთავრული",
        "მხედრული",
        "ასომთავრული",
        "ნუსხური",
        "ლათინური",
        "კირილური"
      ],

      allowedOutputs: [
        "მთავრული",
        "მხედრული",
        "ასომთავრული",
        "ნუსხური",
        "ხუცური",
        "შანიძისეული",
        "ტფილელისეული",
        "ფონეტიკური",
        "ტრანსკრიფცია"
      ],

      lettersToScript: {
        "ᲐᲑ": "მთავრული",
        ⴀⴁ: "ნუსხური",
        ႠႡ: "ასომთავრული",
        აბ: "მხედრული",
        Ⴀბ: "შანიძისეული",
        "Აბ": "ტფილელისეული",
        Ⴀⴁ: "ხუცური",
        ab: "ტრანსკრიფცია",
        ɑb: "ფონეტიკური",
        Аб: "კირილური",
        Ab: "ლათინური"
      },

      scriptToLetters: {
        მთავრული: "ᲐᲑ",
        ნუსხური: "ⴀⴁ",
        ასომთავრული: "ႠႡ",
        მხედრული: "აბ",
        კირილური: "Аб",
        ლათინური: "Ab",
        შანიძისეული: "Ⴀბ",
        ტფილელისეული: "Აბ",
        ხუცური: "Ⴀⴁ",
        ტრანსკრიფცია: "ab",
        ფონეტიკური: "ɑb"
      },

      eng: {
        მთავრული: "mtavruli",
        ნუსხური: "nuskhuri",
        ასომთავრული: "asomtavruli",
        მხედრული: "mkhedruli",
        შანიძისეული: "shanidziseuli",
        ხუცური: "khutsuri",
        ტფილელისეული: "tfileliseuli",
        კირილური: "cyrillic",
        ტრანსკრიფცია: "transcription",
        ფონეტიკური: "phonetic",
        ლათინური: "qwerty"
      }
    };
  },
  methods: {
    unsupportedFunctionalle: function() {
      Materialize.toast(Vue.i18n.translate("ფუნქცია მალე დაემატება"), 3000);
      Materialize.toast(
        Vue.i18n.translate("ეწვიეთ old.anbani.ge წინა ვერსიისთვის"),
        3000
      );

      setTimeout(() => {
        window.open("http://old.anbani.ge", "_blank");
      }, 3500);
    },

    helpCapitalize: function() {
      var messageDuration = 10000;

      if (
        this.helpMessageAllowed == undefined ||
        this.helpMessageAllowed == true
      ) {
        this.helpMessageAllowed = false;
        Materialize.toast(
          Vue.i18n.translate(
            "შუა წინადადებაში ასოების კაპიტალიზაციისთვის გამოიყენეთ აპოსტროფის ნიშანი '"
          ),
          messageDuration
        );
        Materialize.toast(
          Vue.i18n.translate("მაგ.: დ'ავით ა'ღმაშენებელი → Ⴃავით Ⴀღმაშენებელი"),
          messageDuration
        );
      }
      setTimeout(() => {
        this.helpMessageAllowed = true;
      }, messageDuration);
    },

    changeInput: function(event) {
      this.inputScript = event.target.getAttribute("data-script");
      this.changeInputItem(this.scriptToLetters[this.inputScript]);
      this.convert();
    },
    changeOutput: function(event) {
      this.outputScript = event.target.getAttribute("data-script");
      if (
        this.outputScript == "შანიძისეული" ||
        this.outputScript == "ხუცური" ||
        this.outputScript == "ტფილელისეული"
      )
        this.helpCapitalize();
      this.changeOutputItem(this.scriptToLetters[this.outputScript]);
      this.convert();
    },
    switchScripts: function() {
      if (!this.isSwitchDisabled) return null;
      var temp = this.inputScript;
      this.inputScript = this.outputScript;
      this.outputScript = temp;
      if (this.scriptToLetters[this.outputScript] !== undefined)
        this.changeOutputItem(this.scriptToLetters[this.outputScript]);
      if (this.scriptToLetters[this.inputScript] !== undefined)
        this.changeInputItem(this.scriptToLetters[this.inputScript]);

      temp = this.inputText;
      this.inputText = this.outputText;
      this.outputText = temp;
    },
    changeInputItem: function(letters) {
      this.inputItems = this.inputItems
        .filter(x => x !== letters)
        .concat([letters]);
      this.inputScript = this.lettersToScript[letters];
      this.inputHandler();
      this.convert();
    },
    changeOutputItem: function(letters) {
      this.outputItems = this.outputItems
        .filter(x => x !== letters)
        .concat([letters]);
      this.outputScript = this.lettersToScript[letters];
      this.convert();
    },

    inputHandler: function() {
      if (this.isInterpreterOn) {
        if (toolkit.detect(this.inputText) != this.eng[this.inputScript]) {
          this.inputText = toolkit.interpret(
            this.inputText,
            this.eng[this.inputScript]
          );
        }
      }
      this.convert();
    },

    handleInterpreter: function() {
      this.isInterpreterOn = !this.isInterpreterOn;
      if (this.isInterpreterOn)
        Materialize.toast(
          Vue.i18n.translate("ავტომატური კლავიატურა ჩართულია"),
          4000
        );
      else
        Materialize.toast(
          Vue.i18n.translate("ავტომატური კლავიატურა გამორთულია"),
          4000
        );
    },

    convert: function() {
      this.outputText = toolkit.safeConvert(
        this.inputText,
        this.eng[this.inputScript],
        this.eng[this.outputScript]
      );
    },

    copyToClipboard: function() {
      var copyTextarea = document.querySelector(".outputbox textarea");
      copyTextarea.select();

      try {
        document.execCommand("copy");
        document.getSelection().empty();
        Materialize.toast(Vue.i18n.translate("ტექსტი დაკოპირებულია"), 4000);
      } catch (err) {
        Materialize.toast(
          Vue.i18n.translate(
            "თქვენი ბრაუზერი არ უზრუნველყოფს ტექსტის კოპირების ფუნქციას"
          ),
          4000
        );
      }
    }
  },

  filters: {
    wordify: function(value, dir) {
      var wordification = {
        from: {
          მთავრული: Vue.i18n.translate("From Mtavruli"),
          ნუსხური: Vue.i18n.translate("From Nuskhuri"),
          ასომთავრული: Vue.i18n.translate("From Asomtavruli"),
          მხედრული: Vue.i18n.translate("From Mkhedruli"),
          კირილური: Vue.i18n.translate("From Cyrillic"),
          ლათინური: Vue.i18n.translate("From Latin"),
          შანიძისეული: Vue.i18n.translate("From Shanidze style"),
          ტფილელისეული: Vue.i18n.translate("From Tfileli style"),
          ხუცური: Vue.i18n.translate("From Khutsuri"),
          ტრანსკრიფცია: Vue.i18n.translate("From Transcription"),
          ფონეტიკური: Vue.i18n.translate("From Phonetic")
        },
        to: {
          მთავრული: Vue.i18n.translate("To Mtavruli"),
          ნუსხური: Vue.i18n.translate("To Nuskhuri"),
          ასომთავრული: Vue.i18n.translate("To Asomtavruli"),
          მხედრული: Vue.i18n.translate("To Mkhedruli"),
          კირილური: Vue.i18n.translate("To Cyrillic"),
          ლათინური: Vue.i18n.translate("To Latin"),
          შანიძისეული: Vue.i18n.translate("To Shanidze style"),
          ტფილელისეული: Vue.i18n.translate("To Tfileli style"),
          ხუცური: Vue.i18n.translate("To Khutsuri"),
          ტრანსკრიფცია: Vue.i18n.translate("To Transcription"),
          ფონეტიკური: Vue.i18n.translate("To Phonetic")
        }
      };
      if (!value) return "";
      value = value.toString();
      if (dir == "from") {
        return wordification.from[value];
      } else {
        return wordification.to[value];
      }
    }
  },

  computed: {
    computedFontSize: function() {
      if (this.inputText != null && this.inputText != "") {
        if (this.inputText.length < 20) return "2.5em";
        else if (this.inputText.length < 100) return "2em";
        return "1em";
      } else {
        return "2em";
      }
    },

    isInputItemsActive: function() {
      return true;
      return (
        ["მხედრული", "ლათინური", "კირილური"].filter(x => x === this.inputScript)
          .length > 0
      );
    },

    isOutputItemsActive: function() {
      return true;
      return (
        ["მხედრული", "ასომთავრული", "ნუსხური", "მთავრული"].filter(
          x => x === this.outputScript
        ).length > 0
      );
    },

    isSwitchDisabled: function() {
      return (
        this.allowedInputs.indexOf(this.outputScript) > -1 &&
        this.allowedOutputs.indexOf(this.inputScript) > -1
      );
    },

    lettersCount: function() {
      if (this.outputText == undefined || this.outputText.length == 0)
        return "";
      return this.outputText.split(" ").length + ":" + this.outputText.length;
    },

    phonetic: function() {
      if (this.inputText == null) return "";
      let n = this.inputText.split(" ");
      let lastWord = n[n.length - 1] == "" ? n[n.length - 2] : n[n.length - 1];
      return toolkit.safeConvert(
        lastWord,
        this.eng[this.inputScript],
        "phonetic"
      );
    }
  }
};
</script>

<style scoped>
.converter {
  display: flex;
  flex-direction: column;
}

.social {
  margin-top: 100px;
  width: 100%;
  text-align: center;
}

.fb-like-mobile {
  display: none;
}
.converterBackground {
  position: absolute;
  margin-top: 64px;
  background-color: #283593;
  height: 350px;
  width: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  display: flex;
  justify-content: center;
  padding-top: 110px;
  animation: fadein 0.5s;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.scriptSwitcher {
  margin-top: 50px;
  justify-content: center;
  display: flex;
  flex-direction: row;
  animation: slide-from-top 1s;
}

.scriptSwitcher > div {
  margin: 0 100px;
}

.boxes {
  display: flex;
  flex-wrap: wrap;
  animation: slide-from-bottom 1s;
}

@keyframes slide-from-bottom {
  0% {
    transform: translateY(10%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes slide-from-top {
  0% {
    transform: translateY(-30%);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes script-item-fade-from-left {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes script-item-fade-from-right {
  0% {
    transform: translateX(100%);
  }

  100% {
    transform: translateX(0);
  }
}

.dropdown {
  display: flex;
  justify-content: center;
}

.dropdown-button {
  width: 300px;
  font-size: 1.5em;
  user-select: none;
}

.scripts {
  position: absolute;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  margin: 20px 0 10px;
  top: 50px;
}

.inputarea .scripts {
  width: 70px;
  left: -80px;
  animation: script-item-fade-from-right 1s;
}

.outputarea .scripts {
  animation: script-item-fade-from-left 1s;
  width: 70px;
  right: -80px;
}

.script {
  font-family: "DejaGeoSans";
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 900;
  color: #aaa;
  background-color: #eee;
  margin: -3px;
  cursor: pointer;
  transition: 0.2s;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  user-select: none;
}

.script:hover {
  background-color: #3f51b5;
  color: #fff;
}

.scripts.on .script.active {
  background-color: #3f51b5;
  color: #fff;
  transform: scale(1.5);
  margin: 5px 0;
  z-index: 2;
}

.dropdown-content {
  min-width: 260px;
}

.scriptIcon {
  font-family: "DejaGeoSans";
  color: #aaa;
  padding-right: 20px;
  width: 50px;
  text-align: right;
  position: absolute;
  right: 0;
}

.actions {
  position: absolute;
  width: 100%;
  bottom: 10px;

  display: flex;
  flex-direction: row;
  padding: 0 10px;
}

.outputarea .actions {
  flex-direction: row-reverse;
}

.actions .action {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin: -3px;
  font-size: 1.5em;
  color: #777;
  cursor: pointer;
  border-radius: 50%;
}

.actions .action:hover {
  color: #333;
}

.actions .action.active {
  color: #283593;
}

.boxarea {
  position: relative;
  min-width: 300px;
  flex-grow: 1;
  min-height: 250px;
}

.inputbox {
  margin-right: 10px;
}

.outputbox {
  margin-left: 10px;
}

.inputbox,
.outputbox {
  position: relative;
  margin-top: 50px;
  border-radius: 4px;
  border: 1px solid #eee;
  background-color: #fff;
  height: 100%;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.indicator {
  color: #aaa;
  position: absolute;
  bottom: 15px;
}

.inputbox .indicator {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.5em;
  bottom: 12px;
  right: 20px;
}

.outputbox .indicator {
  left: 20px;
}

.inputbox textarea,
.outputbox textarea {
  font-family: "DejaGeoSans";
  z-index: 10;
  padding: 20px;
  font-size: 2em;
  outline: none;
  border: none;
  resize: none;
  width: 100%;
  white-space: pre-wrap;
  height: calc(100% - 50px);
}

#reverseBtn {
  top: 115px;
  cursor: pointer;
  width: 60px;
  height: 60px;
  align-items: center;
  justify-content: center;
  line-height: 0.8em;
  color: #fff;
  font-size: 2em;
  z-index: 100;
  display: flex;
  background-color: rgba(255, 255, 255, 0);
}

#reverseBtn:hover {
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  transition: 0.3s;
}

#reverseBtn:active {
  background-color: rgba(255, 255, 255, 0.8);
  transition: 0.2s;
  color: #283593;
}

#reverseBtn.disabled:hover {
  background: none;
}

#reverseBtn.disabled {
  opacity: 0.2;
  cursor: not-allowed;
}

@media only screen and (max-width: 950px) {
  .fb-like {
    display: none;
  }

  .fb-like-mobile {
    display: inline-block;
    margin-bottom: 120px;
  }

  .scriptSwitcher {
    flex-direction: column;
    align-items: center;
  }

  .scriptSwitcher > div {
    padding: 10px;
  }

  .boxarea {
    margin-bottom: 10px;
  }

  .converterBackground {
    margin: 0;
    height: 100%;
  }

  .inputbox {
    margin-right: 0 !important; 
    margin-left: 0 !important;
  }

  .outputbox {
    margin-left: 0 !important;
    margin-right: 0 !important; 
  }

  .inputarea .scripts {
    display: none;
    left: 80px;
    z-index: 2;
  }

  .outputarea .scripts {
    display: none;
    right: -10px;
    z-index: 2;
  }
}
</style>


