<template>
  <div id="app">
    <app-header></app-header>
    <div class="appcontent">
      <transition name="component-fade">
        <router-view></router-view>
      </transition>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
export default {
  name: "app",
  mounted() {
    (function(i, s, o, g, r, a, m) {
      i["GoogleAnalyticsObject"] = r;
      (i[r] =
        i[r] ||
        function() {
          (i[r].q = i[r].q || []).push(arguments);
        }),
        (i[r].l = 1 * new Date());
      (a = s.createElement(o)), (m = s.getElementsByTagName(o)[0]);
      a.async = 1;
      a.src = g;
      m.parentNode.insertBefore(a, m);
    })(
      window,
      document,
      "script",
      "//www.google-analytics.com/analytics.js",
      "ga"
    );
    ga("create", "UA-68949004-1", "auto");
    ga("send", "pageview");
  }
};
</script>

<style>
@charset "UTF-8";

@font-face {
  font-family: "DejaGeoSans";
  src: url("assets/fonts/DejaGeoSans.eot");
  src: url("assets/fonts/DejaGeoSans.eot?#iefix") format("embedded-opentype"),
    url("assets/fonts/DejaGeoSans.woff2") format("woff2"),
    url("assets/fonts/DejaGeoSans.woff") format("woff"),
    url("assets/fonts/DejaGeoSans.ttf") format("truetype"),
    url("assets/fonts/DejaGeoSans.svg#DejaGeoSans") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "DejaGeoSerif";
  src: url("assets/fonts/DejaGeoSerif.eot");
  src: url("assets/fonts/DejaGeoSerif.eot?#iefix") format("embedded-opentype"),
    url("assets/fonts/DejaGeoSerif.woff2") format("woff2"),
    url("assets/fonts/DejaGeoSerif.woff") format("woff"),
    url("assets/fonts/DejaGeoSerif.ttf") format("truetype"),
    url("assets/fonts/DejaGeoSerif.svg#DejaGeoSerif") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "anbani";
  src: url("assets/fonts/anbani.eot");
  src: url("assets/fonts/anbani.eot?#iefix") format("embedded-opentype"),
    url("assets/fonts/anbani.woff") format("woff"),
    url("assets/fonts/anbani.ttf") format("truetype"),
    url("assets/fonts/anbani.svg#anbani") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "anbani" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="a-"]:before,
[class*=" a-"]:before {
  font-family: "anbani" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.a-keyboard:before {
  content: "\61";
}
.a-interpret:before {
  content: "\62";
}
.a-copy:before {
  content: "\63";
}
.a-star:before {
  content: "\64";
}
.a-link:before {
  content: "\65";
}
.a-reverse:before {
  content: "\66";
}

textarea {
  font-family: "DejaGeoSans", sans-serif !important;
}

.script {
  font-family: "DejaGeoSans", serif !important;
}

#app {
  position: relative;
  min-height: 100vh;
}

.component-fade-enter-active {
  transition: 0.5s ease;
}

.component-fade-leave-active {
}

.component-fade-enter,
.component-fade-leave-to {
  opacity: 0;
}
</style>


