  export default {
    name: 'Vkbd',
    mounted() {

          for (var i = 1; i < 11; i++) {
            $("#kbdDiacritics .key" + i + " .normal").html(this.vkbd.layouts["დიაკრიტიკები"][i]);
          }

          for (var i = 1; i < 60; i++) {
            $("#kbdMkhedruli .key" + i + " .shift").html(this.vkbd.layouts["მხედრული"]["s" + i]);
            $("#kbdMkhedruli .key" + i + " .normal").html(this.vkbd.layouts["მხედრული"][i]);
          }

          for (var i = 1; i < 60; i++) {
            $("#kbdMtavruli .key" + i + " .normal").html(this.vkbd.layouts["მთავრული"][i]);
            $("#kbdMtavruli .key" + i + " .shift").html(this.vkbd.layouts["მთავრული"]["s" + i]);

          }

          for (var i = 1; i < 60; i++) {
            $("#kbdAsomtavruli .key" + i + " .normal").html(this.vkbd.layouts["ასომთავრული"][i]);
            $("#kbdAsomtavruli .key" + i + " .shift").html(this.vkbd.layouts["ასომთავრული"]["s" + i]);

          }

          for (var i = 1; i < 60; i++) {
            $("#kbdNuskhuri .key" + i + " .normal").html(this.vkbd.layouts["ნუსხური"][i]);
            $("#kbdNuskhuri .key" + i + " .shift").html(this.vkbd.layouts["ნუსხური"]["s" + i]);

          }


          // for (var i = 1; i < 60; i++) {
          // 	$("#kbdLatin .key"+i+" .normal").html(this.vkbd.layouts["ლათინური"][i]);
          // }


          for (var i = 1; i < 60; i++) {
            $("#kbdSymbols .key" + i + " .normal").html(this.vkbd.layouts["სიმბოლოები"][i]);
            $("#kbdSymbols .key" + i + " .shift").html(this.vkbd.layouts["სიმბოლოები"]["s" + i]);
          }

        this.vkbd.insert = function (char, target) {
          target.focus();
          if (document.selection) {
            var range = document.selection.createRange();
            if (range) {
              range.text = char;
            }
          } else if (target.selectionStart != undefined) {
            var scroll = target.scrollTop, start = target.selectionStart, end = target.selectionEnd;
            var value = target.value.substr(0, start) + char + target.value.substr(end, target.value.length);
            target.value = value;
            target.scrollTop = scroll;
            target.setSelectionRange(start + char.length, start + char.length);
          } else {
            target.value += char;
            target.setSelectionRange(target.value.length, target.value.length);
          }
        }

        $('#vkbdWrapper').on('mousedown', function (e) {
          var node = $(this);
          var position = node.offset();
          var initialized = {
            x: position.left - e.pageX,
            y: position.top - e.pageY
          };
          var handlers = {
            mousemove: function (e) {
              node.css({
                left: (initialized.x + e.pageX) + 'px',
                top: (initialized.y + e.pageY) + 'px'
              });
            },
            mouseup: function (e) {
              $(this).off(handlers);
            }
          };
          $(document).on(handlers);
        });

        this.vkbd.destination = $("#inputArea");

        $("#vkbd .key42").on("click", function () {
          if (this.vkbd.states.shiftState == false) {
            this.vkbd.states.shiftState = true;
            $("#vkbd .key42").addClass("active");
            $("#vkbd .key .normal").addClass("hidden");
            $("#vkbd .key .shift").removeClass("hidden");
          }
          else {
            this.vkbd.states.shiftState = false;
            $("#vkbd .key42").removeClass("active");
            $("#vkbd .key .shift").addClass("hidden");
            $("#vkbd .key .normal").removeClass("hidden");
          }
        });

        $("#vkbd .key").on("click", function () {
          if (!$(this).hasClass("special")) {
            if (this.vkbd.states.shiftState) {
              this.vkbd.insert($(this).find(".shift").html(), this.vkbd.destination[0])
              // this.vkbd.destination.val(this.vkbd.destination.val()+$(this).find(".shift").html());
              $("#vkbd .key42").trigger("click");
            }
            else
              this.vkbd.insert($(this).find(".normal").html(), this.vkbd.destination[0])
            // this.vkbd.destination.val(this.vkbd.destination.val()+$(this).find(".normal").html());

          }
          else {
            // BUG : backspace doesnt work on current position
            switch ($(this).attr("class").match(/\d+/)[0]) {
              case "14":
                // Backspace
                var text = this.vkbd.destination.val()
                this.vkbd.destination.val(text.substr(0, text.length - 1))
                break;
              case "41":
                // Enter
                this.vkbd.insert("\n", this.vkbd.destination[0])
                break;
              case "57":
                // Space
                this.vkbd.insert(" ", this.vkbd.destination[0])
                break;
              default:
                break;
            }
          }

          $("#inputArea").focus();
          converter.convert(converter.data.scriptFrom, converter.data.scriptTo);
          converter.transliteration();
        });


        $("#vkbd .rightBtn").on("click", function () {
          if (this.vkbd.states.shiftState)
            $("#vkbd .key42").trigger("click");

          var leftLayout = $(".layouts").css("left");
          leftLayout = leftLayout.substr(0, leftLayout.length - 2);

          var leftScript = $("#vkbd .scripts").css("left");
          leftScript = leftScript.substr(0, leftScript.length - 2);

          if ($("#vkbd .script.active").is(":last-child")) {
            $("#vkbd .layouts").animate({ "left": leftLayout * 1 + 5 * 500 }, 500);

            $("#vkbd .scripts").animate({ "left": leftScript * 1 + 5 * 120 }, 500);
            $("#vkbd .script.active").removeClass("active").parent().children(":first").addClass("active");
          }
          else {
            $("#vkbd .layouts").animate({ "left": leftLayout * 1 - 500 }, 200);
            $("#vkbd .scripts").animate({ "left": leftScript * 1 - 120 }, 200);
            $("#vkbd .script.active").removeClass("active").next().addClass("active");
          }

        });

        $("#vkbd .leftBtn").on("click", function () {
          if (this.vkbd.states.shiftState)
            $("#vkbd .key42").trigger("click");


          var leftLayout = $(".layouts").css("left");
          leftLayout = leftLayout.substr(0, leftLayout.length - 2);

          var leftScript = $("#vkbd .scripts").css("left");
          leftScript = leftScript.substr(0, leftScript.length - 2);

          if ($("#vkbd .script.active").is(":first-child")) {
            $("#vkbd .layouts").animate({ "left": leftLayout * 1 - 5 * 500 }, 500);

            $("#vkbd .scripts").animate({ "left": leftScript * 1 - 5 * 120 }, 500);
            $("#vkbd .script.active").removeClass("active").parent().children(":last").addClass("active");
          }
          else {
            $("#vkbd .layouts").animate({ "left": leftLayout * 1 + 500 }, 200);

            $("#vkbd .scripts").animate({ "left": leftScript * 1 + 120 }, 200);
            $("#vkbd .script.active").removeClass("active").prev().addClass("active");
          }
        });


