<style scoped>
  .clearfix:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
  }

  .clearfix {
    display: inline-block;
  }
  /* start commented backslash hack \*/

  * html .clearfix {
    height: 1%;
  }

  .clearfix {
    display: block;
  }
  /* close commented backslash hack */

  #vkbd {
    max-width: 400px;
  }

  #vkbd .header {
    text-align: center;
    margin: 10px 0;
  }

  #vkbd .header>div {
    display: inline-block;
    margin: 0 10px;
  }

  #vkbd .header .scriptsWrapper {
    z-index: 0;

    overflow: hidden;
    width: 380px;
    height: 20px;
  }

  #vkbd .header .scripts {

    width: 1000px;
    position: relative;
    left: -120px;
  }

  #vkbd .header .scripts .active {
    color: #333 !important;
    /*transition: 0.3s ease;*/
  }

  #vkbd .header .scripts .script {
    /*transition: 0.3s ease;*/
    text-align: center;
    width: 120px;
    color: #ccc;
    margin: 0 5px;
    float: left;
    display: inline;
  }

  #vkbd .header .leftBtn {
    padding: 5px 10px 5px 6px;
    float: left;
  }

  #vkbd .header .rightBtn {
    padding: 5px 6px 5px 10px;
    float: right;
  }

  #vkbd .header .leftBtn,
  #vkbd .header .rightBtn {
    z-index: 2;
    color: #888;
    border: 1px solid rgba(0, 0, 0, 0);
    border-radius: 3px;
    transition: 0.2s ease;
  }

  #vkbd .header .leftBtn:hover,
  #vkbd .header .rightBtn:hover {
    cursor: pointer;
    background-image: -webkit-linear-gradient(#e8e8e8, #eee);
    background-image: -o-linear-gradient(#e8e8e8, #eee);
    background-image: linear-gradient(#e8e8e8, #eee);
    border: 1px solid #ccc;
    transition: 0.2s ease;
  }

  #vkbd .layouts {
    position: relative;
    left: -1000px;
    width: 3000px;
    height: 180px;
    float: left;
  }

  #vkbd .layouts>div {
    vertical-align: top;
  }

  #vkbd .layout,
  #vkbd .specialLayout {
    cursor: default;
    display: inline-block;
    text-align: center;
    width: 480px;
    height: 180px;
    margin: 0 10px;
  }

  #vkbd .kbdRow {
    text-align: center;
    width: 100%;
    height: 14%;
    margin-top: 1%;
    margin-bottom: 1%;
  }

  #vkbd .key {

    color: #444;
    font-size: 16px;
    margin: 0 auto;
    border: 1px solid #ddd;
    background-image: -webkit-linear-gradient(#e8e8e8, #eee);
    background-image: -o-linear-gradient(#e8e8e8, #eee);
    background-image: linear-gradient(#e8e8e8, #eee);
    background-color: #555;
    height: 100%;
    width: 27px;
    display: inline-block;
    transition: 0.3s ease;
  }

  #vkbd .special {
    background-image: -webkit-linear-gradient(#dddddd, #d0d0d0);
    background-image: -o-linear-gradient(#dddddd, #d0d0d0);
    background-image: linear-gradient(#dddddd, #d0d0d0);
  }


  #vkbd .key span {
    padding: 1px;
    text-align: center;
  }

  #vkbd span.hidden {
    display: none;
  }

  #vkbd .key:hover {
    border-color: #ccc;
    -webkit-box-shadow: 1px 1px 3px #ccc;
    box-shadow: 1px 1px 3px #ccc;
    background-image: -webkit-linear-gradient(#dddddd, #d0d0d0);
    background-image: -o-linear-gradient(#dddddd, #d0d0d0);
    background-image: linear-gradient(#dddddd, #d0d0d0);
  }

  #vkbd .key:active {
    background-image: -webkit-linear-gradient(#eee, #e8e8e8);
    background-image: -o-linear-gradient(#eee, #e8e8e8);
    background-image: linear-gradient(#eee, #e8e8e8);
    background-color: #555;
    -webkit-box-shadow: inset 1px 1px 8px #ddd;
    box-shadow: inset 1px 1px 8px #ddd;
  }

  #vkbd .key.active {
    border-color: #59f;
    color: #59f;
    transition: 0.3s;
  }
  /************Each key**********/

  #vkbd .key.disabled {
    background: none;
    border-color: #eee;
    color: #eee;
  }

  #vkbd .key.disabled:hover {
    background: none;
    border-color: #ddd;
    -webkit-box-shadow: none;
    box-shadow: none;
  }
