<template>
  <div class="footer">
    <div class="text">
      {{ $t('Project Author')}} ჻ {{ $t('George Gach')}}<br> Anbani.ge © 2015-2018 ჻ dev@anbani.ge
    </div>
    <div class="alphabet">
      <span v-for="(item, index) in script.mkhedruli" class="letter" v-bind:key="item">
        <span class="a">{{ fromScript[index] }}</span>
        <span class="b">{{ script["mkhedruli"][index] }}</span>
      </span>
    </div>
  </div>

</template>

<script>
  import data from '../scripts/data.js';

  export default {
    name: 'Footer',
    mounted() {
      var scripts = ["asomtavruli", "nuskhuri", "mtavruli"]
      setInterval(_ => {
        this.fromScript = this.script[scripts[(new Date()).getMinutes() % 3]]
      }, 1000)
    },

    computed : {
        
    },    
    data() {
      return {
        fromScript : '',
        script : {
          "mkhedruli": "აბგდევზთიკლმნოპჟრსტუფქღყშჩცძწჭხჯჰ",
          "asomtavruli": "ႠႡႢႣႤႥႦႧႨႩႪႫႬႭႮႯႰႱႲႳႴႵႶႷႸႹႺႻႼႽႾႿჀ",
          "nuskhuri": "ⴀⴁⴂⴃⴄⴅⴆⴇⴈⴉⴊⴋⴌⴍⴎⴏⴐⴑⴒⴓⴔⴕⴖⴗⴘⴙⴚⴛⴜⴝⴞⴟⴠ",
          "mtavruli": "ᲐᲑᲒᲓᲔᲕᲖᲗᲘᲙᲚᲛᲜᲝᲞᲟᲠᲡᲢᲣᲤᲥᲦᲧᲨᲩᲪᲫᲬᲭᲮᲯᲰ",
        }
        
      }
    }
  }

</script>

<style scoped>
  .footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding-bottom: 2px;
    display: flex;
    flex-direction: column;
    background-color: #3f51b5;
  }

  .text {
    background-color: #283593;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 20px 0 20px 30px;
    font-family: sans-serif;

    color: rgba(255, 255, 255, 0.9);
    line-height: 1.8em;
  }

  .alphabet {
    font-family: 'DejaGeoSans';
    height: 50px;
    background-color: #3f51b5;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px 0 20px;
    cursor: default;
    user-select: none;
  }



  .letter {
    height: 100%;
    width: 100%;
    line-height: 3em;
    position: relative;
    cursor: pointer;
    padding: 10px 10px;
    color: #eee;
    font-size: 1.2em;
  }

  .letter > span 
  {
    top:-0.1em;
    position: absolute;
  }

  .letter .b {
    opacity: 0;
    transition: opacity 0.4s;
  }

  .letter:hover .a{
    opacity: 0;
    transition: opacity 0.4s;
  }

  .letter:hover .b
  {
    transition: opacity 0.4s;
    opacity: 1;
  }

  @media screen and (orientation:portrait) and (max-width: 800px){
    .alphabet {
      display: none
    }
  }
</style>


